.tur-wraper-input input[type="search"] {
  border: 1px solid #eeeeee;
  padding: 10px;
  border-radius: 4px;
}

.member-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
  border-bottom: 1px solid #eeeeee;
}

.member-name {

}

.member-mount {
  color: #0969ff;
}


.button-add {
  position: absolute;
  bottom: 90px;
  font-size: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  background-color: #0969ff;
  color: #ffffff;
  text-align: center;
}

select {
  margin-top: 5px;
}

.tur-transaction-header {
  text-align: center;
}

.tur-transaction-header input {
  font-size: 30px;
  text-align: center;
  font-weight: bold;
  margin-bottom: 30px;
}

.tur-transaction-action {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}

.tur-transaction-action input {
  border: 0;
}

.tur-transaction-action input[type="file"] {
  visibility: hidden;
  position: absolute;
  top: -1px;
}

.tur-transaction-action label:first-child i {
  display: block;
  font-size: 30px;
}

.tur-transaction-action label {
  width: 48%;
  display: block;
  padding: 15px;
  background-color: #eeeeee;
  border: 1px solid #dddddd;
  text-align: center;
  font-weight: bold;
  border-radius: 4px;
}

